@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;1,700&display=swap);

svg path {
    stroke-width: 3px;
}

.active {
    opacity: 1 !important;
}

.calendar .DayPicker {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
        0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.removeTodoAction {
    /* display: none; */
    width: 0px;
    transform: scale(0);
    transition: all 0.2s ease-out;
}
.tag:hover .removeTodoAction {
    /* display: inline-block; */
    width: auto;
    transform: scale(1);
    margin-left: 0.5rem;
}

.todo-item .circle {
    padding: 1px;
    width: 20px;
    height: 20px;
    min-width: 20px;
}
.done-todo {
    max-height: 519px;
    box-shadow: -1px 0 1px 1px rgba(243, 243, 243, 0.6),
        inset -1px 0 0 1px rgba(243, 243, 243, 0.6);
}

@media (max-width: 768px) {
    .timeline {
        height: calc(100% - 60px);
    }
}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif, -apple-system, BlinkMacSystemFont,
        'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
html,
body,
#root {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: hidden;
}
.hiddenScrollbar::-webkit-scrollbar {
    width: 0px; /* Remove scrollbar space */
    background: transparent; /* Optional: just make scrollbar invisible */
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    display: none;
}

